/* eslint-disable react/no-array-index-key */

import Mute from './Mute.style';
import Pre from './Pre.style';

type Props = { mainTfContent: string[]; bucketName: string };

const copyToClipBoard = (text: string) => {
  const textField = document.createElement('textarea');
  textField.setAttribute(
    'style',
    'position: absolute; bottom: 0; top: 0; opacity: 0.01; height: 1px; width: 1px'
  );
  textField.innerText = text;
  document.body.appendChild(textField);
  textField.select();
  document.execCommand('copy');
  textField.remove();
};

const Code = ({ mainTfContent, bucketName }: Props) => {
  const lines = [
    '# Create a folder to store infrastructure code',
    '',
    'mkdir infrastructure',
    'cd infrastructure',
    '',
    '# Create config file',
    `cat <<EOT >> ${bucketName || 'main'}.tf`,
    ...mainTfContent,
    'EOT',
    '',
    '# Deploy',
    '',
    'terraform init',
    'terraform apply'
  ];
  return (
    <Pre>
      {lines.map((line, i) => {
        if (line.trim().startsWith('#')) {
          return <Mute key={i}>{line}{'\n'}</Mute>
        }
        return <span key={i}>{line}{'\n'}</span>
      })}
    </Pre>
  );
};

export default Code;
